<template>
  <div class="newsview">
    <ul class="resultList" v-for="item in usermsg" :key="item.index">
      <li>
        <div class="resultTop">
          <h4>{{item.date}}</h4>
          <div class="resultinfo">
            <ul>
              <li>
                <span>{{item.income}}</span>
                <span></span>
              </li>
              <li>
                <span>{{item.TaxPayment}}</span>
                <span></span>
              </li>
              <li>
                <span>{{item.TaxableIncome}}</span>
                <span></span>
              </li>
               <li>
                <span>{{item.TaxRate}}</span>
                <span></span>
              </li>
            </ul>
          </div>
        </div>
        <div class="resultBottom" style="display:none;">
          <div class="resultBottom1">
            <span></span>
            <span>{{item.TaxItem}}</span>
          </div>
           <div class="resultBottom2">
            <span></span>
            <span>{{item.company}}</span>
          </div>
        </div>
        <div class="_show">
          <img class="showimg" src="../assets/down.png" alt="">
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import '../lib/js/jquery-1.11.0.min.js'
$(function(){

  $(document).on("click","._show",function(){

      if(!$(this).children().hasClass('toggleimg')) {
        $(this).children().addClass('toggleimg')
        $(this).prev().css({
          "display":"block"
        });
      }else {
        $(this).children().removeClass('toggleimg')
        $(this).prev().css({
          "display":"none"
        });
      }
  })
})
export default {
  name: 'newsview',
  data () {
    return {
      msg: '',
      usermsg:{}
    }
  },
  methods:{
    
  },
  created () {
    var username = localStorage.getItem("realname")
    var idnumer  = localStorage.getItem("cardid") 
    // alert(username)
    // alert(idnumer)
    var querydate  = localStorage.getItem("queryyear");
    var cityname = this.$store.cityname 
    //http://192.168.88.21:8080/jmportal4.0.0/interfaces/incometaxquery.do
     this.axios.get('http://jmpotal.hanweb.com/jmp/interfaces/incometaxquery.do', {
      params: {
          // name:username,//
          // idNumber:idnumer,//

          name:"",//
          idNumber:"159357",//
          year:querydate,
          cityname:cityname
          }
        })
          .then( res => {
            
            var data = res.data
            this.usermsg = data

            localStorage.removeItem("realname")
            localStorage.removeItem("cardid")
        })
          .catch(function (error) {
          console.log(error);
        });
    },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
  .newsview {
    padding: 10px;
  }
  .newsview .resultList {
    width: 100%;
    box-sizing: border-box;
  }
  .newsview .resultList li {
    margin: 0;
    width: 93%;
    border: 1px solid #e7e7e7;
    padding: 10px;
  }
  .resultTop h4 {
    margin-top: 0;
    font-weight: 100;
    font-size: 15px;
  }
  .resultinfo {
    overflow: hidden;
  }
  .resultTop .resultinfo ul li span:nth-of-type(1) {
    color: #FED517;
  }
  .resultTop .resultinfo ul li {
    width: 25%;
    float: left;
    border: none;
    text-align: center;
    font-size: 14px;
    color: #999;
    padding: 0;
  }
  .resultTop .resultinfo ul li span {
    display: block;
    margin: 10px 0 5px 0;
  }
  .resultBottom {
    border-top: 1px solid #e7e7e7;
    margin-top: 10px;
  }
  .resultBottom span {
    font-size: 14px;
    color: #999;
    display: inline-block;
    margin: 6px 0 6px 0;
  }
  
  .resultBottom .resultBottom1 {
    margin: 6px 0 6px 0;
  }
  .resultBottom .resultBottom2 {
    margin: 6px 0 6px 0;
  }
  .resultBottom .resultBottom1 span:nth-of-type(2) {
    float: right;
  }
  .resultBottom .resultBottom2 span:nth-of-type(2) {
    float: right;
  }
  ._show {
    width: 100%;
    height: 16px;
  }
  ._show img {
    display: block;
    width: 16px;
    height: 16px;
    margin: 0 auto;
  }
  .toggleimg {
    transform: rotate(180deg)
  }
</style>



