        height: 40px;
        background-color: #00558e;
        position: absolute;
        top: 0;
        right: 0;
    }
    .botton_dow {
	border-radius: 0;
	height: 36px;
	width: 11%;
	float: left;
    }
    .botton_dow:after {
        border-left: 5px solid transparent;  
        border-right: 5px solid transparent;  
        border-top: 5px solid #fff;  
        content: "";  
        position: absolute;
        width: 0;
        top: 40%;
        right: 35%;
    }
