import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/project/HelloWorld'
import queryview from '../project/queryview.vue'
import newsview from '../project/newsview.vue'


Vue.use(Router)

export default new Router({
  model:history,
  routes: [
    {
      path: '/',
      name: 'queryview',
      component: queryview
    },
    {
      path: '/queryview',
      name: 'queryview',
      component: queryview
    },
    {
      path: '/newsview',
      name: 'newsview',
      component: newsview
    },
  ]
})



